:host .filter-flyout {
  &__header {
    position: sticky;
    top: 0;
    padding-right: var(--mdx-comp-filter-flyout-elements-header-padding-right);
    padding-left: var(--mdx-comp-filter-flyout-elements-header-padding-left);
    padding-top: var(--mdx-comp-filter-flyout-elements-header-padding-top);
    background-color: var(--mdx-comp-filter-flyout-elements-header-background-color-default);
    border-width: var(--mdx-comp-filter-flyout-border-width-default);
    border-top-right-radius: var(--mdx-comp-filter-flyout-border-radius);
    border-top-left-radius: var(--mdx-comp-filter-flyout-border-radius);
  }

  &__container {
    position: relative;
    display: flex;
    align-items: center;
    border-bottom: solid;
    border-color: var(--mdx-comp-filter-flyout-elements-header-border-color-default);
    border-width: var(--mdx-comp-filter-flyout-elements-header-border-width-default);
    padding-bottom: var(--mdx-comp-filter-flyout-elements-footer-padding-default);

    &-button {
      position: absolute;
      padding: 0;
      background-color: transparent;
      border: none;
      padding-right: 12px;
      padding-left: 12px;
      padding-top: 8px;
      padding-bottom: 8px;
      right: 0;
      cursor: pointer;

      &:focus-visible {
        border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
        outline-color: var(--mdx-comp-keyboard-focus-color-default);
        outline-style: solid;
        outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
        outline-offset: 2px;
      }
    }

    ::slotted([slot='header']) {
      margin: 0;
      padding: 0;
      font: var(--mdx-comp-filter-flyout-elements-header-font-title);
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      padding-right: var(--mdx-comp-filter-flyout-elements-header-spacing-gap-title-badge);
    }
  }

  &__icon {
    color: var(--mdx-comp-filter-flyout-elements-header-icon-color-default);

    &:hover {
      color: var(--mdx-comp-filter-flyout-elements-header-icon-color-hover);
    }
  }
}
