:host .filter-flyout__footer {
  display: flex;
  flex-direction: column;
  gap: var(--mdx-comp-filter-flyout-elements-footer-spacing-vertical-gap);

  position: sticky;
  bottom: 0;
  padding: var(--mdx-comp-filter-flyout-elements-footer-padding-default);
  background-color: var(--mdx-comp-filter-flyout-elements-footer-background-color-default);
  border-top: solid;
  border-color: var(--mdx-comp-filter-flyout-elements-footer-border-color-default);
  border-width: var(--mdx-comp-filter-flyout-elements-footer-border-top);
  border-bottom-right-radius: var(--mdx-comp-filter-flyout-border-radius);
  border-bottom-left-radius: var(--mdx-comp-filter-flyout-border-radius);
}
