:host {
  flex-shrink: 1;
  overflow-y: auto;
  overflow-x: hidden;
  flex-grow: 1;
}

:host .filter-flyout {
  &__list {
    padding-top: var(--mdx-comp-filter-flyout-elements-header-spacing-gap-rows);
    gap: var(--mdx-comp-filter-flyout-elements-header-spacing-gap-rows);
    padding-right: var(--mdx-comp-filter-filter-flyout-list-padding-right);
    padding-left: var(--mdx-comp-filter-filter-flyout-list-padding-left);
    padding-bottom: var(--mdx-comp-filter-filter-flyout-list-padding-bottom);
  }

  &__controls {
    position: relative;
    margin-bottom: var(--mdx-comp-filter-flyout-elements-header-spacing-gap-rows);

    slot[name='controls'] {
      display: flex;
      gap: var(--mdx-comp-filter-flyout-elements-header-spacing-gap-links-dot-spacer);
      flex-direction: row;
      align-items: center;
    }

    &-button {
      position: absolute;
      background-color: transparent;
      border: none;
      padding-right: 12px;
      padding-left: 12px;
      padding-top: 8px;
      padding-bottom: 8px;
      right: 0;

      &:focus-visible {
        border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
        outline-color: var(--mdx-comp-keyboard-focus-color-default);
        outline-style: solid;
        outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
        outline-offset: 2px;
      }
    }
  }
}
