import { Component, h, Prop } from '@stencil/core';
import { isExternalUrl } from '../../utils';
import { LINK_SIZE, LINK_VARIANTS } from './exports';

/**
 * @slot label - Slot for the label / content of the link
 */
@Component({
  tag: 'mdx-link',
  styleUrl: 'link.scss',
  shadow: true,
})
export class Link {
  @Prop()
  href: string;

  @Prop()
  ariaLabel: string;

  @Prop({ reflect: true })
  variant: LINK_VARIANTS = LINK_VARIANTS.RUNNINGTEXT;

  @Prop({ reflect: true })
  size: LINK_SIZE = LINK_SIZE.MEDIUM;

  @Prop()
  target: '_blank' | '_self' | '_parent' | '_top' = '_self';

  private isExternalURL() {
    return isExternalUrl(this.href);
  }

  render() {
    return (
      <a
        class="link"
        aria-label={this.ariaLabel}
        href={this.href}
        target={this.target}
        rel={this.isExternalURL() ? 'noreferrer' : undefined}
      >
        <slot></slot>
      </a>
    );
  }
}
