import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../../../icons/exports';

/**
 * @slot header - for the heading and badge in the header
 */

@Component({
  tag: 'mdx-filter-flyout-header',
  styleUrl: 'filter-flyout-header.scss',
  shadow: true,
})
export class FilterFlyoutHeader {
  @Prop()
  isOpen: boolean;

  @Event()
  filterFlyoutClose: EventEmitter<void>;

  @Prop()
  closeButtonAriaLabel: string;

  private handleClose() {
    this.isOpen = false;
    this.filterFlyoutClose.emit();
  }

  render() {
    return (
      <div class="filter-flyout__header">
        <div class="filter-flyout__container">
          <slot name="header" />
          <button
            class="filter-flyout__container-button"
            onClick={() => this.handleClose()}
            aria-label={this.closeButtonAriaLabel}
          >
            <mdx-icon-x size={ICON_SIZES.SM} class="filter-flyout__icon" />
          </button>
        </div>
      </div>
    );
  }
}
