:host .link {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  cursor: pointer;
  color: var(--mdx-comp-link-color-default);

  &:hover {
    color: var(--mdx-comp-link-color-hover);
  }
}

:host([size='small'][variant='runningText']) .link {
  text-decoration: var(--mdx-comp-link-small-decoration-runningtext);
  font: var(--mdx-comp-link-small-font-runningtext);
}
:host([size='medium'][variant='runningText']) .link {
  text-decoration: var(--mdx-comp-link-medium-decoration-runningtext);
  font: var(--mdx-comp-link-medium-font-runningtext);
}
:host([size='large'][variant='runningText']) .link {
  text-decoration: var(--mdx-comp-link-large-decoration-runningtext);
  font: var(--mdx-comp-link-large-font-runningtext);
}

:host([size='small'][variant='standalone']) .link {
  text-decoration: var(--mdx-comp-link-small-decoration-standalone);
  gap: var(--mdx-comp-link-gap-icon-text-default);
  font: var(--mdx-comp-link-small-font-standalone);
}
:host([size='medium'][variant='standalone']) .link {
  text-decoration: var(--mdx-comp-link-medium-decoration-standalone);
  gap: var(--mdx-comp-link-gap-icon-text-default);
  font: var(--mdx-comp-link-medium-font-standalone);
}
:host([size='large'][variant='standalone']) .link {
  text-decoration: var(--mdx-comp-link-large-decoration-standalone);
  gap: var(--mdx-comp-link-gap-icon-text-default);
  font: var(--mdx-comp-link-large-font-standalone);
}
