:host .filter-flyout {
  position: relative;
  display: flex;
  flex-direction: column;
  box-shadow: var(--mdx-comp-filter-flyout-shadow-default);
  background-color: var(--mdx-comp-filter-flyout-background-color-color);
  border-color: var(--mdx-comp-filter-flyout-border-color-default);
  border-width: var(--mdx-comp-filter-flyout-border-width-default);
  border-radius: var(--mdx-comp-filter-flyout-border-radius);
  max-height: var(--mdx-comp-filter-flyout-sizing-max-height);
  width: var(--mdx-comp-filter-flyout-sizing-width);

  @media screen and (max-width: 768px) {
    position: fixed;
    inset: 0;
    width: 100%;
    max-height: 100vh;
    max-height: -webkit-fill-available;
    border-width: var(--mdx-comp-filter-overlay-border-width-default);
    border-color: var(--mdx-comp-filter-overlay-border-color-default);
    background-color: var(--mdx-comp-filter-overlay-background-color-color);
  }
}
