import { Component, Listen, Prop, h } from '@stencil/core';

/**
 * @slot - for the header, list and footer of the filter flyout
 */

@Component({
  tag: 'mdx-filter-flyout',
  styleUrl: 'filter-flyout.scss',
  shadow: true,
})
export class FilterFlyout {
  @Prop()
  open = false;

  @Listen('filterFlyoutClose', { target: 'window' })
  handleClose() {
    this.open = false;
  }

  render() {
    return this.open ? (
      <div class="filter-flyout">
        <slot />
      </div>
    ) : null;
  }
}
